
.login {
    display: grid;
    place-items: center;
    height: 100vh;
    width: 100%;
}

.login__logo > img {
    width: 800px;
    object-fit: contain;
}

.login > button {
    width: 300px;
    background-color: #738adb;
    color: #eff2f5;
    font-weight: 8--;
}

.login > button:hover {
    background-color: black;
    color: #738adb;
}